$schoolColor: #95afc0;
$textColorBlack: #1e272e;
$schoolInfoColor: #ffa801;

.main {
    &-outer {
        min-width: 1320px;
        height: auto;
        padding: 15px;
    }
    
    &-inner { 
        border: 1px solid $textColorBlack;
        border-radius: 2.5px;

        width: 100%;
        height: auto;

        &-school {
            position: relative;
            text-align: center;
            cursor: pointer;
            color: $textColorBlack;
            background-color: $schoolColor;
            padding: 8px;    

            &-id {
                position: absolute;
                left: 1%;
                padding-right: 10px;
                border-right: 1px solid $textColorBlack;
            }
        }

        &-schoolInfo {
            width: 100%;
            height: 0px;
            overflow: hidden;
            white-space: nowrap;
            display: flex;
            justify-content: space-around;
            align-items: center;
            background-color: $schoolInfoColor;
            font-size: 14px;
            cursor: pointer;
            transition: .5s;

            &-id {
                border-right: 1px solid $textColorBlack;
                padding-right: 10px;
            }
        }
    }
}